body {
  background: #3c404e;;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
}

.input-field {
  width: 220px;
  height: 30px;
  background: none;
  margin-right: 15px;
  outline: none;
  border-radius: 30px;
  border: none;
  background-color: transparent;
  box-shadow: 3px 3px 3px #222327, -3px -3px 3px #363636;
  color: #fff;
  padding: 0 15px;
  font-size: 18px;
}

.btn,
.delete-btn {
  border: none;
  background: none;
  outline: none;
  color: #fff;
  font-size: 14px;
  box-shadow: 3px 3px 3px #222327, -3px -3px 3px #363636;
  width: 80px;
  height: 30px;
  border-radius: 30px;
  cursor: pointer;
}

.delete-btn {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin-left: 150px;
}

.btn:hover,
.input-field:hover,
.delete-btn:hover {
  box-shadow: inset 3px 3px 3px #222327, -3px -3px 3px #363636;
}

.d-f {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
  font-size: 18px;
  color: #fff;
}

.d-f > div {
  cursor: pointer;
}

.update-btn {
  margin: 20px 10px;
}

.all-btn {
  margin: 12px;
  width: 212px;
}
